<template>
  <suspense>
    <template #default>
      <Card>
        <template #header>
          <div class="flex items-center justify-between">
            <h6>Items</h6>
            <div v-if="items">
              <div v-if="!items.isReadonly">
                <Button
                  v-if="
                    items.stateShow.status_id !== 1000 &&
                    items.selectedItems.length > 0 &&
                    items.state.total_qty > 1
                  "
                  :disabled="items.stateShow?.deleted_at != null"
                  :loading="items.stateStatuses.length === 0"
                  class="mr-4"
                  color="teal"
                  dense
                  @click="items.openSplitItemsModal"
                  ><span class="material-icons transform rotate-90 text-lg"
                    >arrow_circle_up</span
                  >
                  Split Buyback
                </Button>
                <Button
                  v-if="
                    items.stateShow?.status_id !== 3 &&
                    items.state.total_qty >= 3
                  "
                  :disabled="items.stateShow?.deleted_at != null"
                  class="mr-4"
                  color="teal"
                  dense
                  @click="items.openDataMatch"
                  ><img
                    alt="data-match-icon"
                    class="inline-block mr-1 w-5"
                    src="@/assets/icon/data-match-icon.svg"
                  />
                  Data Match
                </Button>
                <Button
                  :disabled="items.stateShow?.deleted_at != null"
                  color="teal"
                  dense
                  @click="items.openModal('create')"
                  ><span class="material-icons text-lg">add</span> Add Item
                </Button>
              </div>
            </div>
          </div>
        </template>
        <template #default>
          <ItemList ref="refItems" />
        </template>
      </Card>
    </template>
    <template #fallback>
      <div>
        <LoadingCard />
      </div>
    </template>
  </suspense>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import LoadingCard from 'components/LoadingCard.vue'

import ItemList from './ItemList.vue'

export default defineComponent({
  name: 'ItemCard',
  components: {
    Card,
    Button,
    LoadingCard,
    ItemList
  },
  setup(props) {
    const refItems = ref(null)
    const items = computed(() => {
      return refItems.value || null
    })
    return {
      refItems,
      items
    }
  }
})
</script>

<style lang="scss" scoped></style>
