<template>
  <Breadcrumb :backTo="routeBack" :items="breadcrumb">
    <template #page-title>
      <div class="flex gap-2 items-center flex-wrap">
        <h5>{{ isBulkQuote ? 'Quote Details' : 'Offer Detail' }}</h5>
        <div
          v-if="isBulkQuote"
          class="inline-flex px-4 py-2 bg-blue-lighten text-blue-04 rounded-full"
        >
          Beta
        </div>
      </div>
    </template>
  </Breadcrumb>
</template>

<script>
import { computed, defineComponent } from 'vue'
import { useBulkQuote } from 'composable/bulkquote'

import { useCookie } from 'utils/cookie'
import Breadcrumb from 'components/Breadcrumb.vue'

export default defineComponent({
  components: {
    Breadcrumb
  },

  setup(props) {
    const cookie = useCookie()
    const { isBulkQuote } = useBulkQuote()

    const cacheQuery = cookie.get(`online-buyback`)
    const isHasCache = computed(() => !!cacheQuery)
    const routeBack = computed(() => {
      if (isBulkQuote.value) {
        return {
          name: 'layoutBulkQuote'
        }
      }
      let routeOptions = {
        name: 'buybackOnline',
        params: {
          statusId: 'all',
          category: 'active'
        },
        query: {}
      }
      if (isHasCache.value) {
        const isHasSubStatus = cacheQuery?.params?.subStatusId
        routeOptions.params.statusId =
          parseInt(cacheQuery?.params?.statusId) || 'all'
        routeOptions.params.category = cacheQuery?.params?.category || 'active'
        routeOptions.query = {
          filter: true
        }
        if (!!isHasSubStatus) {
          routeOptions.params.subStatusId =
            cacheQuery?.params?.subStatusId || ''
        }
      }
      return routeOptions
    })
    const breadcrumb = [
      {
        text: isBulkQuote.value ? 'Bulk Quote' : 'Mail-In',
        to: {
          name: isBulkQuote.value ? 'layoutBulkQuote' : 'buybackOnline'
        }
      },
      {
        text: isBulkQuote.value ? 'Quote Details' : 'Offer Details'
      }
    ]
    return {
      isBulkQuote,
      cacheQuery,
      isHasCache,
      routeBack,
      breadcrumb
    }
  }
})
</script>
