<template>
  <div class="grid gap-3">
    <div>
      <DeviceSearch
        ref="refDeviceSearch"
        :query="stateSearch.product.query"
        @update:query="
          (query) => {
            stateSearch.product.query = query
          }
        "
        @search="handleSearchItem"
        @select="handleChangeItem"
        @custom-product="handleSelectCustomItem"
        :products="products"
        :categories="categories"
        :loading="loadingSearch.product"
        :show-add-custom-product="false"
      />
    </div>
    <template v-if="isCustomItem">
      <div>
        <Input
          v-model="selectedItemReconcile.product_name"
          label="Custom Product Name"
          placeholder="Custom product name"
          block
        />
      </div>
    </template>
    <template v-else-if="isMacItem">
      <div>
        <label>Processor</label>
        <BSelect
          v-model="processor"
          :items="specs.mac.Processor.options"
          item-key="id"
          item-name="name"
          block
          placeholder="Processor"
          :max-height="200"
          :loading="loading.getSpecs.Processor"
          :disabled="specs.mac.Processor.options.length === 0"
        >
          <template #prepend>
            <span class="font-semibold block mx-6 my-4">Select Processor</span>
          </template>
        </BSelect>
      </div>

      <div>
        <label>Memory</label>
        <BSelect
          v-model="memory"
          :items="specs.mac.Memory.options"
          item-key="id"
          item-name="name"
          block
          placeholder="Memory"
          :max-height="200"
          :loading="loading.getSpecs.Memory"
          :disabled="specs.mac.Memory.options.length === 0"
        >
          <template #prepend>
            <span class="font-semibold block mx-6 my-4">Select Memory</span>
          </template>
        </BSelect>
      </div>
      <div>
        <label>Hard Drive</label>
        <BSelect
          v-model="hardDrive"
          :items="specs.mac['Hard Drive'].options"
          item-key="id"
          item-name="name"
          block
          placeholder="Hard Drive"
          :max-height="200"
          :loading="loading.getSpecs['Hard Drive']"
          :disabled="specs.mac['Hard Drive'].options.length === 0"
        >
          <template #prepend>
            <span class="font-semibold block mx-6 my-4">Select Hard Drive</span>
          </template>
        </BSelect>
      </div>
      <div>
        <label>Graphic Card</label>
        <BSelect
          v-model="graphicCard"
          :items="specs.mac['Graphics Card'].options"
          item-key="id"
          item-name="name"
          block
          placeholder="Graphic Card"
          :max-height="200"
          :loading="loading.getSpecs['Graphics Card']"
          :disabled="specs.mac['Graphics Card'].options.length === 0"
        >
          <template #prepend>
            <span class="font-semibold block mx-6 my-4"
              >Select Graphic Card</span
            >
          </template>
        </BSelect>
      </div>
    </template>
    <div>
      <label>Color</label>
      <BSelect
        v-model="color"
        :items="colorOptions"
        item-key="id"
        item-name="name"
        block
        placeholder="Color"
        :max-height="200"
        :loading="loading.getSpecs.color"
        :disabled="specs.color.options.length === 0"
      >
        <template #prepend>
          <span class="font-semibold block mx-6 my-4">Select Color</span>
        </template>
      </BSelect>
    </div>
    <div>
      <label>Condition</label>
      <BSelect
        v-model="condition"
        :items="conditionOptions"
        item-key="name"
        item-name="name"
        block
        placeholder="Condition"
        :max-height="200"
        :loading="loading.getSpecs.condition"
        :disabled="specs.condition.options.length === 0"
      >
        <template #prepend>
          <span class="font-semibold block mx-6 my-4">Select Condition</span>
        </template>
      </BSelect>
    </div>
    <div v-if="condition.rule.length > 0">
      <p v-html="condition.rule[0]"></p>
      <ul class="my-2">
        <template v-for="(rule, ruleIndex) of condition.rule">
          <li v-if="ruleIndex > 0" class="flex items-start">
            <i class="material-icons mr-4">check_circle</i>
            <p v-html="rule" class="mt-1"></p>
          </li>
        </template>
      </ul>
    </div>
    <div>
      <Input
        v-model="itemDetail.price"
        block
        label="Payout"
        placeholder="Payout"
        type="number"
        :currency="true"
        icon
        icon-block
      >
        <template #icon>
          <Currency only-symbol class="mx-2" />
        </template>
      </Input>
    </div>
    <div>
      <Input
        v-model="itemDetail.qty"
        @focus-out="handleSetQty"
        block
        label="Qty"
        placeholder="Qty"
        type="number"
      />
    </div>
    <div v-if="selectedItemReconcile.imei_required == 1 && itemDetail.qty < 100">
      <table class="bulk-imei-notes">
        <thead>
          <tr>
            <th>IMEI/SN</th>
            <th>Notes</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item of itemDetail.imeiNotes">
            <td>
              <Input v-model="item.imei" placeholder="IMEI/SN" block />
            </td>
            <td>
              <Input v-model="item.notes" placeholder="Notes" block />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, watch } from 'vue'

import { useReconcileQuoteItem } from 'composable/bulkquote'
import { useSearch as useSearchProduct } from 'composable/product'

import BSelect from 'components/BSelect/BSelect.vue'
import Input from 'bbUi/components/input/input.vue'
import Currency from 'components/Currency.vue'
import DeviceSearch from 'components/Device/Form/DeviceSearch.vue'
import debounce from 'utils/debounce'

export default defineComponent({
  name: 'BulkQuoteReconcile',
  components: {
    Currency,
    BSelect,
    Input,
    DeviceSearch
  },
  props: {
    quoteId: {
      type: String,
      required: true
    },
    item: {
      type: Object,
      default: () => ({})
    },
    valid: {
      type: Boolean,
      default: false
    },
    maxQty: {
      type: Number,
      default: 99
    }
  },
  emits: ['update:color', 'update:valid'],
  setup(props, { emit }) {
    const {
      selectedItem: selectedItemReconcile,
      specs,
      isMacItem,
      isCustomItem,
      isValid,
      itemDetail,
      loading,
      setSelectedItem,
      setSpecMac,
      getColors,
      getSpecsByProductId,
      getConditions,
      reconcileQuoteItem
    } = useReconcileQuoteItem()
    const {
      state: stateSearch,
      loading: loadingSearch,
      searchProduct,
      unsetProductSearchResult
    } = useSearchProduct()

    const color = computed({
      get: () => ({
        id: specs.color.id,
        name: specs.color.name
      }),
      set: (selectedColor) => {
        specs.color.id = selectedColor.id
        specs.color.name = selectedColor.name
      }
    })
    const condition = computed({
      get: () => {
        let rule = []
        try {
          rule =
            typeof specs.condition.rule === 'string'
              ? JSON.parse(specs.condition.rule) || []
              : specs.condition.rule || []
        } catch (err) {
          console.warn(`invalid parsing condition rule: ${err.message}`)
        }
        return {
          id: specs.condition.id,
          name: specs.condition.name,
          rule,
          price: specs.condition.price
        }
      },
      set: (selectedCondition) => {
        specs.condition.id = selectedCondition.id
        specs.condition.name = selectedCondition.name
        specs.condition.rule = selectedCondition.rule || []
        specs.condition.price = selectedCondition.price
        itemDetail.price = selectedCondition.price
      }
    })
    const processor = computed({
      get: () => ({
        id: specs.mac.Processor.id,
        name: specs.mac.Processor.name
      }),
      set: (item) => {
        setSpecMac({
          key: 'Processor',
          item: {
            id: item.id,
            name: item.name
          }
        })
      }
    })
    const hardDrive = computed({
      get: () => ({
        id: specs.mac['Hard Drive'].id,
        name: specs.mac['Hard Drive'].name
      }),
      set: (item) => {
        setSpecMac({
          key: 'Hard Drive',
          item: {
            id: item.id,
            name: item.name
          }
        })
      }
    })
    const memory = computed({
      get: () => ({
        id: specs.mac.Memory.id,
        name: specs.mac.Memory.name
      }),
      set: (item) => {
        setSpecMac({
          key: 'Memory',
          item: {
            id: item.id,
            name: item.name
          }
        })
      }
    })
    const graphicCard = computed({
      get: () => ({
        id: specs.mac['Graphics Card'].id,
        name: specs.mac['Graphics Card'].name
      }),
      set: (item) => {
        setSpecMac({
          key: 'Graphics Card',
          item: {
            id: item.id,
            name: item.name
          }
        })
      }
    })

    const colorOptions = computed(() => specs.color.options)
    const conditionOptions = computed(() => specs.condition.options)
    const products = computed(() => stateSearch.product.result.products)
    const categories = computed(() => stateSearch.product.result.categories)
    const originalItem = computed(() => props.item)

    const setItem = async (selectedItem) => {
      setSelectedItem(selectedItem)
      stateSearch.product.query = selectedItemReconcile.product_name
      handleSearchItem()
      await getColors()
      if (isMacItem.value) {
        await getSpecsByProductId({
          quoteId: props.quoteId
        })
      } else {
        await getConditions({
          quoteId: props.quoteId
        })
      }
    }
    const handleSetColor = (selectedColor) => {
      const { id: selectedId = null, name: selectedName = null } =
        selectedColor || {}
      specs.color.id = selectedId
      specs.color.name = selectedName
    }
    const handleSetCondition = (selectedCondition) => {
      const { id: selectedId = null, name: selectedName = null } =
        selectedCondition || {}
      specs.condition.id = selectedId
      specs.condition.name = selectedName
    }
    const handleSetQty = () => {
      if (itemDetail.qty < 1) return
      const remainder = itemDetail.qty - itemDetail.imeiNotes.length
      if (remainder < 0)
        itemDetail.imeiNotes.splice(remainder, Math.abs(remainder))
      else {
        for (let i = 0; i < remainder; i++)
          itemDetail.imeiNotes.push({
            imei: '',
            notes: ''
          })
      }
    }
    const handleSearchItem = debounce(async () => {
      await searchProduct({ query: stateSearch.product.query })
    }, 500)
    const handleChangeItem = async (item) => {
      stateSearch.product.query = item?.product_name || item?.name || ''
      const currentItemId = selectedItemReconcile.id
      await setItem(item)
      selectedItemReconcile.id = currentItemId
    }
    const handleSelectCustomItem = () => {
      unsetProductSearchResult()
    }
    const submitReconcileItem = async () => {
      return await reconcileQuoteItem({ quoteId: props.quoteId })
    }

    watch(() => props.item, setItem)
    watch(
      isValid,
      (valid) => {
        emit('update:valid', valid)
      },
      {
        immediate: true
      }
    )

    onMounted(async () => {
      if (props.item) await setItem(props.item)
    })

    return {
      stateSearch,
      products,
      categories,
      loading,
      loadingSearch,
      selectedItemReconcile,
      specs,
      itemDetail,
      colorOptions,
      conditionOptions,
      color,
      condition,
      processor,
      hardDrive,
      memory,
      graphicCard,
      isMacItem,
      isCustomItem,
      isValid,
      originalItem,
      handleSetColor,
      handleSetCondition,
      handleSetQty,
      handleSearchItem,
      handleChangeItem,
      handleSelectCustomItem,
      submit: submitReconcileItem
    }
  }
})
</script>

<style scoped lang="scss">
.bulk-imei-notes {
  @apply w-full;
  thead > tr {
    @apply bg-grey-01;
  }

  th {
    @apply text-left;
  }

  th,
  td {
    @apply p-4;
  }
}
</style>
