<template>
  <div class="flex items-center gap-4">
    <Tooltip
      v-if="state?.deleted_at != null"
      content="Archived"
      placement="top"
    >
      <div
        class="flex items-center justify-center w-12 h-12 bg-orange-lighten rounded-full text-orange-04 mx-4 select-none"
      >
        <i class="material-icons">archive</i>
      </div>
    </Tooltip>
    <template v-if="isBulkQuote">
      <BulkQuoteDetailStatus
        @update-status="handleBulkQuoteStatusUpdate"
        :status="state?.order_status"
        :loading="loadingShow"
        :quote-id="$route.params.buybackId"
        :disabled="bulkQuote.restrictedQuoteStatusUpdate"
        :order="order"
      />
    </template>
    <template v-else>
      <div class="rounded-md bg-white p-3 items-center" v-loading="isLoading">
        <div class="flex justify-between items-center">
          <h6>Status :</h6>
          <Badge
            :color="color(state?.order_status?.short_name || '')"
            class="ml-2 mr-2 text-status"
            view="smooth"
          >{{ state?.order_status?.status_name }}</Badge
          >
          <StatusesBtn :data="state" />
        </div>
      </div>
    </template>
    <div class="flex items-center">
      <div v-if="isReady" class="mr-4">
        <div v-if="state.is_offered == 0 && state.type == 4">
          <Button
            @click="handleCustomOffer"
            :loading="loadingCustomOffer.offer"
            class="mr-2"
            color="teal"
            ><i class="material-icons">local_offer</i> Offer</Button
          >
          <Button
            @click="handleRejectCustomOffer"
            :loading="loadingCustomOffer.rejectOffer"
            color="red"
            ><i class="material-icons">close</i> Reject Offer</Button
          >
        </div>
      </div>
      <Dropdown placement="bottom" :hide-on-click="false">
        <template #default>
          <Button :style="{ padding: '.8rem' }" color="teal">
            <i class="material-icons">more_vert</i>
          </Button>
        </template>
        <template #dropdown>
          <DropdownMenu>
            <Tooltip placement="left" :disabled="!isDisabledPrintShippingKit">
              <DropdownItem
                :class="
                  isDisabledPrintShippingKit
                    ? 'el-dropdown-menu__item--disabled'
                    : ''
                "
                v-loading="loadingPrint.shippingKit"
                @click="printShippingKit"
              >
                <i class="material-icons mr-2 text-xl">print</i> Shipping Kit
              </DropdownItem>
              <template #content>
                <p>
                  Unable to generate a shipping label. <br/> Please contact support
                  for more detail
                </p>
              </template>
            </Tooltip>
            <template v-if="isReady">
              <DropdownItem
                v-if="!isBulkQuote"
                v-loading="loadingPrint.receipt"
                :class="isDisabledPrintReceipt ? 'el-dropdown-menu__item--disabled' : ''"
                @click="printReceipt"
                ><i class="material-icons mr-2 text-xl">insert_drive_file</i>
                Print Receipt</DropdownItem
              >
            </template>
            <template v-else>
              <DropdownItem
                ><span class="text-grey-02"
                  ><i class="material-icons mr-2 text-xl">print</i> Print
                  Receipt</span
                ></DropdownItem
              >
            </template>
            <DropdownItem
              v-if="!isBulkQuote"
              v-loading="loadingOriginalQuote"
              @click="showOriginalQuote"
              ><img
                src="@/assets/icon/show-original.svg"
                alt="show-original"
                class="inline-block mr-2"
              />
              Show Original</DropdownItem
            >
            <DropdownItem @click="openChangelog">
              <img
                src="@/assets/icon/changelog.svg"
                alt="changelog"
                class="inline-block mr-2"
              />
              Changelog
            </DropdownItem>
            <DropdownItem
              v-if="state?.deleted_at == null"
              @click="handleArchiveModal"
            >
              <Button class="my-2" color="orange" dense block>
                <i class="material-icons">archive</i> Archive {{ isBulkQuote? 'Quote' : 'Order' }}
              </Button>
            </DropdownItem>
            <DropdownItem
              v-if="state?.deleted_at != null"
              @click="handleUnarchiveModal"
            >
              <Button class="my-2" color="orange" dense block>
                <i class="material-icons">archive</i> Restore {{ isBulkQuote? 'Quote' : 'Order' }}
              </Button>
            </DropdownItem>
          </DropdownMenu>
        </template>
      </Dropdown>
    </div>
    <ShowOriginal v-model="modalOriginalQuote" />
  </div>
  <Modal
    size="xs"
    v-model="archiveModal"
    title="Archive Order"
    hide-header
    hide-footer
    @close="handleCloseArchiveModal"
  >
    <div class="py-10">
      <img
        class="mb-10 mx-auto"
        src="@/assets/icon/attention-orange.svg"
        alt="Success Icon"
      />
      <div class="mt-8 mb-10">
        <h5 class="mb-4 text-center">Archiving this Order</h5>
        <p class="text-center mb-4">
          Are you sure you want to archive this order? <br />
          Once archived, orders can be recovered from the "Archived" menu
        </p>
      </div>
      <div class="text-center">
        <Button
          view="outlined"
          color="teal"
          class="bg-white"
          :disabled="archiveLoading"
          @click="archiveModal = false"
          >Cancel</Button
        >
        <Button
          color="orange"
          class="ml-3"
          :loading="archiveLoading"
          @click="archiveOrder"
          >Yes, Archive</Button
        >
      </div>
    </div>
  </Modal>
  <Modal
    size="xs"
    v-model="unarchiveModal"
    title="Archive Order"
    hide-header
    hide-footer
    @close="handleCloseUnarchiveModal"
  >
    <div class="py-10">
      <img
        class="mb-10 mx-auto"
        src="@/assets/icon/attention-orange.svg"
        alt="Success Icon"
      />
      <div class="mt-8 mb-10">
        <h5 class="mb-4 text-center">Restoring this Order</h5>
        <p class="text-center mb-4">
          Are you sure you want to restore this order ?
        </p>
      </div>
      <div class="text-center">
        <Button
          view="outlined"
          color="teal"
          class="bg-white"
          :disabled="unarchiveLoading"
          @click="unarchiveModal = false"
          >Cancel</Button
        >
        <Button
          color="orange"
          class="ml-3"
          :loading="unarchiveLoading"
          @click="unarchiveOrder"
          >Yes, Restore</Button
        >
      </div>
    </div>
  </Modal>
  <BuybackShippingKitDownload
    :id="state?.box_id"
    :shippings="state?.shipping_label"
  />
</template>

<script>
import { computed, defineComponent, ref } from 'vue'
import {
  usePrint,
  useShow,
  useShowOriginalQuote,
  useCustomOffer
} from 'composable/buyback'
import { useBulkQuote } from 'composable/bulkquote'
import { useArchive, useUnarchive } from 'composable/buyback'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'

import { useRoute, useRouter } from 'vue-router'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Badge from 'bbUi/components/badge/badge.vue'

import ShowOriginal from './ShowOriginal.vue'
import StatusesBtn from './StatusesBtn.vue'
import BulkQuoteDetailStatus from 'components/BulkQuote/Detail/BulkQuoteDetailStatus.vue'
import BuybackShippingKitDownload from 'components/Features/Buyback/BuybackShipping/BuybackShippingKitDownload.vue'

import { ElMessage } from 'element-plus'
import { color } from 'utils/status'

export default defineComponent({
  components: {
    Button,
    Modal,
    Badge,
    ShowOriginal,
    StatusesBtn,
    BulkQuoteDetailStatus,
    BuybackShippingKitDownload
  },
  props: {
    bulkQuote: {
      type: Object,
      default: () => ({
        restrictedQuoteUpdate: false,
        restrictedQuoteItemUpdate: false,
        restrictedQuoteStatusUpdate: false,
        restrictedQuotePercentageUpdate: false,
        isReconcilable: false
      })
    },
    order: {
      type: Object,
      default: undefined
    }
  },
  setup(props) {
    const storePiniaBuybackShipping = useBuybackShipping()
    const { isBulkQuote } = useBulkQuote()
    const {
      data: archiveData,
      modal: archiveModal,
      loading: archiveLoading,
      actionArchive,
      unsetData: archiveUnsetData
    } = useArchive('online')
    const {
      data: unarchiveData,
      modal: unarchiveModal,
      loading: unarchiveLoading,
      actionUnarchive,
      unsetData: unarchiveUnsetData
    } = useUnarchive('online')
    const {
      state,
      loading: loadingShow,
      action: actionShow
    } = useShow('online')
    const {
      actionShippingKit,
      actionReceipt,
      loading: loadingPrint
    } = usePrint('online')
    const {
      state: stateOriginalQuote,
      loading: loadingOriginalQuote,
      modal: modalOriginalQuote,
      openModal: openModalOriginalQuote,
      action: actionOriginalQuote
    } = useShowOriginalQuote()
    const {
      actionOffer,
      actionRejectOffer,
      loading: loadingCustomOffer
    } = useCustomOffer()

    const route = useRoute()
    const router = useRouter()

    const isLoading = computed(() => {
      return state.value?.order_status == null
    })
    const isDisabledPrintShippingKit = computed(
      () =>
        state.value?.shipping_label_is_generate === 0 ||
        state.value?.shipping_label_status == 2 ||
        loadingShow.value === true
    )
    const isDisabledPrintReceipt = computed(() => state.value?.is_print_receipt == 0)

    const printReceipt = async () => {
      if(isDisabledPrintReceipt.value) return
      const buybackId = state.value.box_id
      await actionReceipt(buybackId)
    }
    const printShippingKit = async () => {
      if (!route.params.buybackId || isDisabledPrintShippingKit.value) return
      
      storePiniaBuybackShipping.shippingKit.modal = true
    }
    const showOriginalQuote = async () => {
      await actionOriginalQuote(route.params.buybackId)
      openModalOriginalQuote()
    }
    const openChangelog = () =>
      router.push({
        name: 'buybackOnlineChangelog',
        params: {
          buybackId: route.params.buybackId,
          ...(route.params.marketerId && { marketerId: route.params.marketerId }),
          ...(route.params.status && { status: route.params.status })
        }
      })
    const isReady = computed(() => {
      return (state.value?.id && true) || false
    })
    const handleCustomOffer = () =>
      actionOffer(route.params.buybackId, async () => {
        ElMessage({
          type: 'success',
          message: 'Offer sent'
        })
        await action(route.params.buybackId)
      })
    const handleRejectCustomOffer = () =>
      actionRejectOffer(route.params.buybackId, async () => {
        ElMessage({
          type: 'success',
          message: 'Offer rejected'
        })
        await action(route.params.buybackId)
      })

    const btnMenuArchive = ref(false)
    const btnMenuUnarchive = ref(false)

    const handleArchiveModal = () => {
      archiveModal.value = true
    }

    const handleCloseArchiveModal = () => {
      archiveModal.value = false
    }

    const handleUnarchiveModal = () => {
      unarchiveModal.value = true
    }

    const handleCloseUnarchiveModal = () => {
      unarchiveModal.value = false
    }

    const archiveOrder = async () => {
      archiveData.id = state.value.id
      await actionArchive(() => {
        ElMessage({
          type: 'success',
          message: 'Success archiving order'
        })
        handleCloseArchiveModal()
        archiveUnsetData()
      })
      actionShow({
        id: route.params.buybackId,
        ...(
          (route.params.marketerId && route.params.marketerId === 'me' && { marketer_id: '' })
          || (route.params.marketerId && { marketer_id: route.params.marketerId })
        )
      })
    }

    const unarchiveOrder = async () => {
      unarchiveData.id = state.value.id
      await actionUnarchive(() => {
        ElMessage({
          type: 'success',
          message: 'Success restoring order'
        })
        handleCloseUnarchiveModal()
        unarchiveUnsetData()
      })
      actionShow({
        id: route.params.buybackId,
        ...(
          (route.params.marketerId && route.params.marketerId === 'me' && { marketer_id: '' })
          || (route.params.marketerId && { marketer_id: route.params.marketerId })
        )
      })
    }
    const handleBulkQuoteStatusUpdate = async () => {
      await actionShow({
        id: route.params.buybackId,
        ...(
          (route.params.marketerId && route.params.marketerId === 'me' && { marketer_id: '' })
          || (route.params.marketerId && { marketer_id: route.params.marketerId })
        )
      })
    }

    btnMenuArchive.value = computed(() => state.value.deleted_at != null)
    btnMenuUnarchive.value = computed(() => state.value.deleted_at == null)

    return {
      state,
      stateOriginalQuote,
      loadingOriginalQuote,
      modalOriginalQuote,
      loadingPrint,
      printReceipt,
      printShippingKit,
      openModalOriginalQuote,
      openChangelog,
      showOriginalQuote,
      isReady,
      isDisabledPrintShippingKit,
      isDisabledPrintReceipt,
      handleCustomOffer,
      handleRejectCustomOffer,
      loadingCustomOffer,
      handleArchiveModal,
      handleCloseArchiveModal,
      handleUnarchiveModal,
      handleCloseUnarchiveModal,
      archiveOrder,
      unarchiveOrder,
      archiveModal,
      unarchiveModal,
      archiveLoading,
      unarchiveLoading,
      handleBulkQuoteStatusUpdate,
      btnMenuArchive,
      btnMenuUnarchive,
      color,
      isLoading,
      isBulkQuote,
      loadingShow
    }
  }
})
</script>

<style lang="scss" scoped>
.text-status {
  font-size: 1rem !important;
}
</style>
