<template>
  <div>
    <div>
      <div class="grid gap-4 my-4">
        <div class="flex flex-wrap items-center justify-between gap-4">
          <div class="md:flex-none flex-auto">
            <suspense>
              <template #default>
                <div>
                  <Breadcrumb class="w-full" />
                </div>
              </template>
              <template #fallback>
                <LoadingCard />
              </template>
            </suspense>
          </div>
          <div class="md:flex-none flex-auto">
            <suspense>
              <template #default>
                <Menu :bulk-quote="bulkQuote" :order="stateShow" />
              </template>
              <template #fallback>
                <LoadingCard />
              </template>
            </suspense>
          </div>
        </div>
      </div>
      <div class="my-2">
        <suspense>
          <template #default>
            <OrderInfo />
          </template>
          <template #fallback>
            <LoadingCard />
          </template>
        </suspense>
      </div>
      <div class="grid md:grid-cols-3 grid-cols-1 gap-4 my-2">
        <suspense>
          <template #default>
            <Show
              ref="refQuoteShow"
              :is-bulk-quote="isBulkQuote"
              :bulk-quote="bulkQuote"
            />
          </template>
          <template #fallback>
            <LoadingCard />
          </template>
        </suspense>
      </div>
      <div v-if="isBulkQuote" class="my-2 mb-6">
        <Card>
          <template #header>
            <div class="flex flex-wrap items-center justify-between">
              <h6>Items</h6>
              <Button
                v-if="!bulkQuote.restrictedQuoteItemUpdate"
                @click="openModalAddItem"
                :disabled="
                  !isBulkQuoteTabContentRendered || stateShow?.status_id == 2
                "
                color="teal"
                dense
              >
                <i class="material-icons">add</i> Add Items
              </Button>
            </div>
          </template>
          <template #default>
            <div>
              <div class="mb-6 relative">
                <BulkQuoteItemPercentage
                  @update:model-value="setQuotePercentage"
                  :model-value="quotePercentage"
                  :order="stateShow"
                  :disabled="
                    loadingQuoteItem.getBulkQuoteItemInfo ||
                    bulkQuote.restrictedQuotePercentageUpdate
                  "
                />
                <div
                  v-if="hasManuallyPricedItems"
                  @click="confirmManuallyPricedModal = true"
                  class="absolute left-0 top-0 w-full h-full cursor-pointer"
                ></div>
              </div>
              <Tab
                v-if="isShowBulkQuoteTab"
                v-model="selectedTab"
                :items="tabBulkQuote"
                component
                name="name"
                by="value"
              />
              <component
                ref="refBulkQuoteTabContent"
                :key="bulkQuoteComponent.name"
                :is="bulkQuoteComponent.component"
              ></component>
            </div>
          </template>
        </Card>
      </div>
      <div v-else class="my-2 mb-6">
        <suspense>
          <template #default>
            <Items />
          </template>
          <template #fallback>
            <LoadingCard />
          </template>
        </suspense>
      </div>
      <div class="my-2">
        <BuybackImages />
      </div>
      <div class="my-2">
        <suspense>
          <template #default>
            <BuybackNotes />
          </template>
          <template #fallback>
            <LoadingCard />
          </template>
        </suspense>
      </div>
    </div>
    <Modal v-model="modalCreateItem" size="lg">
      <template #header>
        <h6 class="font-medium">Add Item</h6>
      </template>
      <template #default>
        <div
          class="flex gap-4 bg-blue-lighten text-blue-04 px-3 py-2 rounded-lg mb-4"
        >
          <i class="material-icons">info</i>
          <div>
            <p class="mt-1">
              The price indicated is the original price, it hasn't been reduced
              from quoted price.
            </p>
          </div>
        </div>
        <div>
          <h6 v-if="selectedItems.length > 0" class="text-lg font-medium mb-4">
            Detail Item
          </h6>
          <BulkQuoteItemSearch
            @select="handleSelect"
            :clear-on-select="false"
          />
          <BulkQuoteItem v-model:items="selectedItems" />
        </div>
      </template>
      <template #footer>
        <div class="flex flex-wrap justify-between items-center">
          <a href="" @click.prevent="closeModalCreateItem" class="px-4 py-2"
            >Cancel</a
          >
          <Button
            @click="handleCreateItem"
            :loading="loadingCreateItem"
            color="teal"
            >Save
          </Button>
        </div>
      </template>
    </Modal>
    <Modal
      v-model="confirmManuallyPricedModal"
      size="xs"
      hide-header
      hide-footer
    >
      <div class="text-center py-10">
        <img
          class="mb-10 mx-auto"
          src="@/assets/icon/attention-warning.svg"
          alt="Success Icon"
        />
        <div class="mt-8 mb-10">
          <h5 class="mb-4 font-medium">Attention!</h5>
          <p class="my-5">
            There are prices that were changed manually. Manually priced items
            will be returned to the original price before using this function.
          </p>
        </div>
        <Button
          @click="
            () => {
              resetManuallyPricedItems()
              confirmManuallyPricedModal = false
            }
          "
          color="teal"
          class="mr-3"
          >Proceed
        </Button>
        <a @click.prevent="confirmManuallyPricedModal = false" class="py-4 px-6">
          Cancel
        </a>
      </div>
    </Modal>
  </div>
</template>
<script>
import {
  computed,
  defineComponent,
  h,
  onMounted,
  ref,
  Suspense,
  watch
} from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useShow } from 'composable/buyback'
import {
  useBulkQuote,
  useBulkQuoteItem,
  useCreateBulkQuoteItem
} from 'composable/bulkquote'

import Menu from './Menu.vue'
import Breadcrumb from './Breadcrumb.vue'
import OrderInfo from './OrderInfo.vue'
import Show from './Show.vue'
import Items from './Items.vue'
import ItemList from './ItemList.vue'
import BuybackImages from 'components/Features/Buyback/BuybackImages/BuybackImages.vue'
import BuybackNotes from 'components/Features/Buyback/BuybackNotes/BuybackNotes.vue'

import Tab from 'bbUi/components/tab/tab.vue'
import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import LoadingCard from 'components/LoadingCard.vue'
import ContentLoading from 'components/ContentLoading.vue'
import BulkQuoteItem from 'components/BulkQuote/Item/BulkQuoteItem.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import BulkQuoteDetailItem from 'components/BulkQuote/Detail/BulkQuoteDetailItem.vue'
import BulkQuoteItemSearch from 'components/BulkQuote/Item/BulkQuoteItemSearch.vue'
import BulkQuoteItemPercentage from 'components/BulkQuote/Item/BulkQuoteItemPercentage.vue'
import debounce from 'utils/debounce'

export default defineComponent({
  name: 'BuybackOnlineDetail',
  components: {
    Modal,
    BulkQuoteItem,
    Menu,
    Breadcrumb,
    OrderInfo,
    Show,
    Items,
    ItemList,
    BuybackImages,
    BuybackNotes,
    Tab,
    Card,
    Button,
    LoadingCard,
    BulkQuoteDetailItem,
    BulkQuoteItemPercentage,
    BulkQuoteItemSearch
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const { state: stateShow } = useShow('online')
    const { isBulkQuote, selectedTab, tabBulkQuote } = useBulkQuote()
    const {
      selectedItems,
      loading: loadingCreateItem,
      createItem,
      addItem
    } = useCreateBulkQuoteItem({
      multiple: false
    })
    const {
      items: quoteItems,
      quotePercentage,
      loading: loadingQuoteItem,
      confirmManuallyPricedModal,
      hasManuallyPricedItems,
      totalItems,
      resetManuallyPricedItems,
      getBulkQuoteItems: _getBulkQuoteItems,
      getBulkQuoteItemInfo,
      updateQuotePercentage,
      updateQuoteItem,
      deleteQuoteItem
    } = useBulkQuoteItem()

    const modalCreateItem = ref(false)
    const refBulkQuoteTabContent = ref(null)
    const refQuoteShow = ref(null)

    const currentQuoteStatus = computed(() => {
      const { order_status: orderStatus = {} } = stateShow.value || {}
      const { id = null, short_name = null, status_name = null } = orderStatus
      return {
        id,
        short_name,
        status_name
      }
    })
    const isShowBulkQuoteTab = computed(() => {
      const isMatchedStatusId = ['9003'].includes(
        String(currentQuoteStatus.value.id || '')
      )
      return isMatchedStatusId
    })
    const bulkQuote = computed(() => {
      const isArchived = stateShow.value?.deleted_at != null
      const restrictedQuoteUpdate =
        (isBulkQuote.value &&
          ['9001', '9002', '3'].includes(
            String(currentQuoteStatus.value.id || '')
          )) ||
        isArchived

      const restrictedQuoteStatusUpdate = isArchived
      // (isBulkQuote.value &&
      //   ['9001', '3'].includes(String(currentQuoteStatus.value.id || ''))) ||
      // isArchived

      const restrictedQuoteItemUpdate =
        (isBulkQuote.value &&
          ['9001', '9002', '3'].includes(
            String(currentQuoteStatus.value.id || '')
          )) ||
        isArchived

      const restrictedQuotePercentageUpdate =
        currentQuoteStatus.value.id !== 9000

      const isReconcilable =
        (isBulkQuote.value &&
          ['9003', '9004'].includes(
            String(currentQuoteStatus.value.id || '')
          )) ||
        isArchived

      const totalQuoteItems = totalItems.value

      return {
        restrictedQuoteUpdate,
        restrictedQuoteItemUpdate,
        restrictedQuoteStatusUpdate,
        restrictedQuotePercentageUpdate,
        isReconcilable,
        totalQuoteItems
      }
    })
    const bulkQuoteComponent = computed(() => {
      if (selectedTab.value === 'active_item')
        return {
          name: 'BulkQuoteDetailItem',
          component: h(
            BulkQuoteDetailItem,
            {
              onUpdateQuoteStatus,
              onUpdateItems,
              onUpdateBulkQuoteItems,
              onUpdateQuoteItem,
              onDeleteQuoteItem,
              quoteId: route.params.buybackId,
              quotePercentage: quotePercentage.value,
              quoteItems: quoteItems.value,
              reconcile: bulkQuote.value.isReconcilable,
              loading: {
                quoteItem: { ...loadingQuoteItem }
              },
              disableItems: bulkQuote.value.restrictedQuoteItemUpdate,
              disabledStatus:
                !!stateShow?.value &&
                !![9001, 9002, 9003, 9004, 3, 2].includes(
                  stateShow.value?.status_id
                )
            },
            ''
          )
        }
      return {
        name: 'ItemList',
        component: h(Suspense, null, {
          default: () =>
            h(
              ItemList,
              {
                isBulkQuoteReconcile: bulkQuote.value.isReconcilable,
                onReconcile: () => (selectedTab.value = 'active_item')
              },
              ''
            ),
          fallback: () => h(ContentLoading)
        })
      }
    })
    const isBulkQuoteTabContentRendered = computed(
      () => refBulkQuoteTabContent.value !== null
    )
    const isBulkQuoteOffer = computed(() => route.name === 'bulkQuoteDetail')

    const getBulkQuoteItems = async () => {
      await Promise.all([
        _getBulkQuoteItems({ quoteId: route.params.buybackId }),
        getBulkQuoteItemInfo({ quoteId: route.params.buybackId })
      ])
    }
    const onUpdateQuoteStatus = async () => {
      if (refQuoteShow.value)
        await refQuoteShow.value?.actionShow?.({
          id: route.params.buybackId,
          is_store: 1
        })
    }
    const onUpdateItems = (items) => (quoteItems.value = items)
    const onUpdateBulkQuoteItems = (callback) => {
      getBulkQuoteItems({ quoteId: route.params.buybackId }).then(() => {
        if (typeof callback === 'function') callback()
      })
    }
    const onUpdateQuoteItem = (item, callback) => {
      if (item.qty === '' || isNaN(Number(item.qty))) {
        item.qty = 1
      }
      updateQuoteItem({
        quoteId: route.params.buybackId,
        item
      }).then((isSuccess) => {
        if (typeof callback === 'function') callback(isSuccess)
      })
    }
    const onDeleteQuoteItem = (itemId, callback) => {
      deleteQuoteItem({
        quoteId: route.params.buybackId,
        itemId
      }).then((isSuccess) => {
        if (typeof callback === 'function') callback(isSuccess)
        if (isSuccess)
          getBulkQuoteItemInfo({
            quoteId: route.params.buybackId
          }).then(() => {})
      })
    }
    const handleSelect = (item) => {
      addItem(item, { quotePercentage: 100 })
    }
    const closeModalCreateItem = () => {
      modalCreateItem.value = false
      selectedItems.value = []
    }
    const openModalCreateItem = () => {
      modalCreateItem.value = true
    }
    const handleCreateItem = async () => {
      const isSuccess = await createItem({
        quoteId: route.params.buybackId
      })
      if (isSuccess) {
        closeModalCreateItem()
        if (refBulkQuoteTabContent.value)
          await getBulkQuoteItems({
            quoteId: route.params.buybackId
          })
      }
    }
    const openModalAddItem = () => {
      if (bulkQuoteComponent.value.name === 'ItemList') {
        if (!isBulkQuoteTabContentRendered.value) return
        refBulkQuoteTabContent.value?.openModal?.('create')
        return
      }
      openModalCreateItem()
    }
    const handleUpdateQuotePercentage = debounce(async () => {
      const isSuccess = await updateQuotePercentage({
        quoteId: route.params.buybackId
      })
      if (!isSuccess) return console.error('failed to update quote percentage')
    }, 800)
    const setQuotePercentage = (percentage) => {
      quotePercentage.value = percentage
      handleUpdateQuotePercentage()
    }
    const getQuoteItemInfo = async (id) => {
      await getBulkQuoteItemInfo({
        quoteId: route.params.buybackId
      })
    }
    watch(
      () => currentQuoteStatus.value.id,
      async (currentStatusId, oldStatusId) => {
        const hasNotChangedStatus = currentStatusId === oldStatusId
        if (hasNotChangedStatus) return
        if (isBulkQuoteOffer.value && currentStatusId) {
          const isReconciled = ['9004', '3'].includes(
            String(currentStatusId || '')
          )
          if (isReconciled) {
            selectedTab.value = 'reconciled_item'
          } else {
            selectedTab.value = 'active_item'
            if (isBulkQuoteTabContentRendered.value) {
              await getBulkQuoteItems({
                quoteId: route.params.buybackId
              })
            }
          }
          await getQuoteItemInfo(route.params.buybackId)
        }
      }
    )

    onMounted(async () => {
      if (isBulkQuoteOffer.value) {
        await Promise.all([
          getQuoteItemInfo(route.params.buybackId),
          getBulkQuoteItems({ quoteId: route.params.buybackId })
        ])
      }
      if (route.query.ref === 'create-order')
        setTimeout(async () => {
          await router.push({
            ...route,
            query: {}
          })
        }, 10000)
    })

    return {
      stateShow,
      quotePercentage,
      refBulkQuoteTabContent,
      refQuoteShow,
      bulkQuote,
      isBulkQuote,
      isShowBulkQuoteTab,
      tabBulkQuote,
      selectedTab,
      bulkQuoteComponent,
      isBulkQuoteTabContentRendered,
      selectedItems,
      loadingCreateItem,
      modalCreateItem,
      loadingQuoteItem,
      confirmManuallyPricedModal,
      hasManuallyPricedItems,
      handleSelect,
      closeModalCreateItem,
      openModalCreateItem,
      openModalAddItem,
      handleCreateItem,
      setQuotePercentage,
      resetManuallyPricedItems
    }
  }
})
</script>
