<template>
  <div class="mb-8">
    <div class="grid gap-6">
      <div
        v-loading="loading.quoteItem && loading.quoteItem.getBulkQuoteItems"
        :style="{ minHeight: '100px' }"
        class="overflow-x-auto"
      >
        <BulkQuoteItem
          v-model:items="items"
          @delete-item="handleDeleteItem"
          @edit-item="setQuoteItem"
          @reconcile-item="handleReconcileItem"
          :percentage="quotePercentage"
          :disabled="disableItems"
          :disabled-status="disabledStatus"
          :reconcile="reconcile"
        >
          <template #action></template>
        </BulkQuoteItem>
      </div>
    </div>
    <Modal
      v-model="modalReconcile"
      @close="closeModalReconcile"
      title="Reconcile"
    >
      <template #default>
        <div>
          <BulkQuoteReconcile
            ref="refReconcileItem"
            v-model:valid="isValidReconcile"
            :quote-id="quoteId"
            :item="selectedItemReconcile"
          />
          <!-- :max-qty="selectedItemReconcile?.qty || 99" -->
        </div>
      </template>
      <template #footer>
        <div class="flex flex-wrap justify-end">
          <Button
            @click="submitReconcileItem"
            :disabled="!isValidReconcile"
            :loading="loadingReconcileItem"
            color="teal"
          >
            Save Changes
          </Button>
        </div>
      </template>
    </Modal>
  </div>
</template>
<script>
import { computed, defineComponent, ref } from 'vue'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import BulkQuoteItem from 'components/BulkQuote/Item/BulkQuoteItem.vue'
import BulkQuoteItemPercentage from 'components/BulkQuote/Item/BulkQuoteItemPercentage.vue'
import BulkQuoteItemSearch from 'components/BulkQuote/Item/BulkQuoteItemSearch.vue'
import BulkQuoteReconcile from 'components/BulkQuote/BulkQuoteReconcile.vue'

import debounce from 'utils/debounce'

export default defineComponent({
  name: 'BulkQuoteDetailItem',
  components: {
    BulkQuoteItemSearch,
    Card,
    Button,
    Modal,
    BulkQuoteItem,
    BulkQuoteItemPercentage,
    BulkQuoteReconcile
  },
  props: {
    quoteItems: {
      type: Array,
      default: () => []
    },
    quoteId: {
      type: String,
      required: true
    },
    disableItems: {
      type: Boolean,
      default: false
    },
    disabledStatus: {
      type: Boolean,
      default: false
    },
    reconcile: {
      type: Boolean,
      default: false
    },
    quotePercentage: {
      type: Number,
      default: () => 100
    },
    loading: {
      type: Object,
      default: () => ({})
    }
  },
  emits: [
    'updateQuoteStatus',
    'updateItems',
    'updateQuoteItem',
    'deleteQuoteItem',
    'updateBulkQuoteItems'
  ],
  setup(props, { emit }) {
    const refReconcileItem = ref(null)
    const selectedItemReconcile = ref(null)
    const modalReconcile = ref(false)
    const isValidReconcile = ref(false)

    const loadingReconcileItem = computed(() => {
      return refReconcileItem.value
        ? refReconcileItem.value.loading?.reconcile
        : false
    })
    const items = computed({
      get: () => props.quoteItems,
      set: (newItems) => emit('updateItems', newItems)
    })

    const handleDeleteItem = async (selectedItem) => {
      const { id: quoteItemId = null } = selectedItem
      if (!quoteItemId)
        return console.error('failed deleting item, quote item id is required')
      emit('deleteQuoteItem', quoteItemId, (isSuccess) => {
        if (!isSuccess) return console.error('failed deleting item')
      })
    }
    const handleUpdateQuoteItem = debounce(async (item) => {
      emit('updateQuoteItem', item, (isSuccess = false) => {
        if (!isSuccess) return console.error('failed to update quote item')
      })
    }, 800)
    const setQuoteItem = (item) => {
      handleUpdateQuoteItem(item)
    }
    const handleReconcileItem = async (reconciledItem) => {
      selectedItemReconcile.value = reconciledItem
      openModalReconcile()
    }
    const openModalReconcile = () => {
      modalReconcile.value = true
    }
    const closeModalReconcile = () => {
      modalReconcile.value = false
      selectedItemReconcile.value = null
    }
    const submitReconcileItem = async () => {
      if (refReconcileItem.value) {
        const response = await refReconcileItem.value.submit?.()
        if (response) {
          closeModalReconcile()
          const { is_change_status: isUpdatedStatus } = response
          if (isUpdatedStatus) emit('updateQuoteStatus', true)
        }
        emit('updateBulkQuoteItems')
      }
    }

    return {
      refReconcileItem,
      selectedItemReconcile,
      items,
      modalReconcile,
      isValidReconcile,
      loadingReconcileItem,
      handleDeleteItem,
      setQuoteItem,
      handleReconcileItem,
      openModalReconcile,
      closeModalReconcile,
      submitReconcileItem
    }
  }
})
</script>

<style scoped lang="scss"></style>
