<template>
  <div class="flex items-center flex-col">
    <Alert class="mb-4" color="orange" view="smooth" v-if="stateShow?.parent">This buyback is part of <router-link class="mr-2" :to="{name: 'buybackOnlineDetail', params: {buybackId: stateShow.parent.box_id}}">#{{stateShow.parent.box_id}}<i class="material-icons text-lg ml-1">open_in_new</i></router-link></Alert>
    <Alert class="mb-4" color="orange" view="smooth" v-if="stateShow?.split_order && stateShow?.split_order.length > 0">Some of these items have been split into a new order. Please use this order number to view them <router-link class="mr-2" v-for="(item, itemIndex) of stateShow.split_order" :key="itemIndex" :to="{name: 'buybackOnlineDetail', params: {buybackId: item.box_id}}">#{{item.box_id}}<i class="material-icons text-lg ml-1">open_in_new</i></router-link></Alert>
    <Alert class="mb-4" color="orange" view="smooth" v-if="stateShow?.is_accepted == 2 && (stateShow?.order_status.id == 12 && stateShow?.order_status.parent_id == 5)">The customer has declined the offer.</Alert>
    <Alert v-loading="orderOfferLoading.update" class="mb-4" color="orange" view="smooth" v-if="stateShow?.is_accepted == 0 && (stateShow?.order_status.id == 12 && stateShow?.order_status.parent_id == 5)">
      <div class="flex w-full justify-between items-center">
        <p>Waiting for customer response. You can also manually accept or decline the offer here</p>
        <div class="flex">
          <Button color="teal" class="mr-2" dense @click="orderOfferUpdate(1)" :disabled="orderOfferLoading.update">Accept</Button>
          <Button color="red" dense @click="orderOfferUpdate(2)" :disabled="orderOfferLoading.update">Decline</Button>
        </div>
      </div>
    </Alert>
  </div>
</template>

<script>
import { defineComponent} from 'vue'
import { useShow, useShowOrderOffer, useShowStatuses } from 'composable/buyback'
import { useRoute } from 'vue-router'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Button,
    Alert
  },
  props: {
  },
  setup(props) {
    const { state: stateShow, action: actionShow } = useShow('online')
    const { loading: orderOfferLoading, actionUpdate: updateOrderOffer, data: orderOfferData } = useShowOrderOffer('online')
    const { actionAll: actionShowStatuses } = useShowStatuses('online')
    const route = useRoute()

    const orderOfferUpdate = async (id) => {
      orderOfferData.is_accept = id
      await updateOrderOffer(route.params.buybackId, false, async() => {
        await Promise.all([
          actionShow(route.params.buybackId),
          actionShowStatuses(route.params.buybackId)
        ])
        ElMessage({
          message: 'Offer updated!',
          type: 'success'
        })
      })
    }
    return {
      stateShow,
      orderOfferLoading,
      orderOfferUpdate
    }
  }
})
</script>
